.about-me {
  padding-right: 70px;
  padding-left: 70px;
  background-color: #202020;
}

.about-me__title {
  margin: 0px;
  color: #FFF;
  font-family: Inter, Arial, sans-serif;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.88px;
  padding-top: 111px;
  padding-bottom: 23px;
  border-left: 0;
  border-top: 0;
  border-right: 0;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: #DADADA;
}

.about-me__name {
  margin: 0;
  color: #FFF;
  font-family: Inter, Arial, sans-serif;
  font-size: 50px;
  font-style: normal;
  font-weight: 400;
  line-height: 58px;
  letter-spacing: -2px;
  width: 600px;
}

.about-me__subtitle {
  margin: 0;
  color: #FFF;
  font-feature-settings: 'clig' off, 'liga' off;
  font-family: Inter, Arial, sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  margin-top: 18px;
}

.about-me__container {
  padding-top: 67px;
  display: grid;
  grid-template-columns: 600px auto;
  grid-template-rows: auto auto auto auto;
  grid-auto-flow: column dense;
  justify-content: space-between;
  padding-bottom: 102px;
}

.about-me__photo {
  grid-row: span 4;
  width: 270px;
  height: 327px;
}

.about-me__text {
  margin: 0;
  color: #FFF;
  font-feature-settings: 'clig' off, 'liga' off;
  font-family: Inter, Arial, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  padding-top: 26px;
  width: 599px;
}

.about-me__link {
  margin: 0;
  color: #FFF;
  font-family: Inter, Arial, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  padding-top: 99px;
  text-decoration: none;
}

.about-me__link:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media (max-width: 1279px) {
  .about-me {
    padding-right: 50px;
    padding-left: 50px;
  }

  .about-me__title {
    padding-top: 91px;
  }

  .about-me__name {
    font-size: 40px;
    line-height: 40px;
    letter-spacing: -1.6px;
    width: 348px;
  }

  .about-me__subtitle {
    font-size: 12px;
    line-height: 18px;
    margin-top: 15px;
  }

  .about-me__photo {
  width: 255px;
  height: 307px;
  }

  .about-me__text {
    font-size: 12px;
    line-height: 18px;
    padding-top: 20px;
    width: 363px;
  }

  .about-me__link {
    padding-top: 87px;
  }

  .about-me__container {
    grid-template-columns: 364px auto;
    padding-top: 66px;
    padding-bottom: 91px;
  }
}



@media (max-width: 767px) {
  .about-me {
    padding-right: 14px;
    padding-left: 14px;
  }

  .about-me__title {
    font-size: 18px;
    letter-spacing: -0.72px;
    padding-top: 70px;
    padding-bottom: 28px;
  }

  .about-me__name {
    font-size: 30px;
    line-height: normal;
    letter-spacing: -1.2px;
    width: 292px;
    padding-top: 40px;
  }

  .about-me__subtitle {
    font-size: 11px;
    line-height: 16px;
    margin-top: 20px;
  }

  .about-me__text {
    font-size: 11px;
    line-height: 16px;
    width: 292px;
  }

  .about-me__link {
    padding-top: 40px;
  }

  .about-me__container {
    grid-template-columns: 292px;
    grid-template-rows: auto auto auto auto auto;
    grid-auto-flow: row dense;
    padding-top: 60px;
    padding-bottom: 70px;
    justify-content: center;
  }

  .about-me__photo {
    order: -1;
    width: 292px;
    height: 352px;
  }
}

