.techs {
  padding-right: 70px;
  padding-left: 70px;
  background-color: #272727;
}

.techs__title {
  margin: 0px;
  color: #FFF;
  font-family: Inter, Arial, sans-serif;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.88px;
  padding-top: 100px;
  padding-bottom: 23px;
  border-left: 0;
  border-top: 0;
  border-right: 0;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: #DADADA;
}

.techs__subtitle {
  margin: 0;
  color: #FFF;
  text-align: center;
  font-family: Inter, Arial, sans-serif;
  font-size: 50px;
  font-style: normal;
  font-weight: 400;
  line-height: 58px;
  align-items: center;
  padding-top: 90px;
  letter-spacing: -2px;
}

.techs__text {
  margin: 0;
  color: #FFF;
  text-align: center;
  font-family: Inter, Arial, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  padding-top: 26px;
  letter-spacing: -0.56px;
  width: 460px;
  margin: auto;
}

.techs__container {
  padding-top: 100px;
  display: grid;
  grid-template-columns: repeat(7, 90px);
  column-gap: 10px;
  justify-content: center;
  padding-bottom: 100px;
}

.techs__tech {
  margin: 0;
  width: 90px;
  height: 60px;
  border-radius: 10px;
  background: #303030;
  color: #FFF;
  text-align: center;
  font-family: Inter, Arial, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 60px;
  letter-spacing: -0.56px;
}

@media (max-width: 1279px) {
  .techs {
    padding-right: 50px;
    padding-left: 50px;
  }

  .techs__title {
    padding-top: 90px;
  }

  .techs__subtitle {
    padding-top: 80px;
  }

  .techs__text {
    font-size: 12px;
    line-height: 18px;
    padding-top: 22px;
    letter-spacing: -0.48px;
  }

  .techs__container {
    padding-top: 83px;
    padding-bottom: 90px;
    column-gap: 3px;
  }

  .techs__tech {
    width: 84px;
    height: 57px;
    font-size: 12px;
    line-height: 57px;
    letter-spacing: -0.48px;
  }
}

@media (max-width: 767px) {
  .techs {
    padding-right: 14px;
    padding-left: 14px;
  }

  .techs__title {
    padding-top: 70px;
    font-size: 18px;
    letter-spacing: -0.72px;
    padding-bottom: 28px;
    border-bottom-color: #000;
    padding-right: 4px;
    padding-left: 4px;
  }

  .techs__subtitle {
    font-size: 30px;
    line-height: normal;
    padding-top: 60px;
    letter-spacing: -1.2px;
  }

  .techs__text {
    font-size: 11px;
    line-height: 16px;
    padding-top: 24px;
    letter-spacing: -0.44px;
    width: 292px;
    margin: auto;
  }

  .techs__container {
    padding-top: 51px;
    grid-template-columns: repeat(2, 86px);
    padding-bottom: 70px;
    row-gap: 10px;
    column-gap: 6px;
  }
}